* {
  box-sizing: border-box;
}

body {
  background: #111;
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

.gallery {
  width: 100%;
  height: 100vh;
  position: absolute;
  overflow: hidden;
}

.cards {
  width: 14rem;
  height: 18rem;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.cards li {
  text-align: center;
  background-color: #9d7cce;
  border-radius: .8rem;
  width: 14rem;
  height: 18rem;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-size: 2rem;
  line-height: 18rem;
  list-style: none;
  position: absolute;
  top: 0;
  left: 0;
}

.actions {
  position: absolute;
  bottom: 25px;
  left: 50%;
  transform: translateX(-50%);
}

button {
  background: #414141 -moz-linear-gradient(top, #575757, #414141);
