  background-image: linear-gradient(#575757, #414141);
  border: none;
  border-radius: 4px;
  outline: none;
  margin: 0 auto 10px;
  padding: 12px 25px;
  font-family: Signika Negative, sans-serif;
  font-size: 13px;
  font-weight: 600;
  line-height: 18px;
  text-decoration: none;
  display: inline-block;
  box-shadow: 0 1px 0 414141px;
}

